import * as React from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Checkbox from "@mui/material/Checkbox";
import IconButton from "@mui/material/IconButton";
import CommentIcon from "@mui/icons-material/Comment";

export default function LayerList({
  layerItmes,
  setSelectedShape,
  updateLayerSelection,
}) {
  const [checked, setChecked] = React.useState([0]);

  React.useEffect(() => {
    updateLayerSelection.current = updateListSel;
  }, [updateLayerSelection]);
  // const { layerItmes, setSelectedShape } = props;
  if (layerItmes === undefined) return null;
  // console.log(layerItmes);

  function updateListSel(selectedObject) {
    layerItmes.forEach(function (item) {
      if (item.id === selectedObject.id) {
        const currentIndex = checked.indexOf(item);
        const newChecked = [...checked];
        if (currentIndex === -1) {
          newChecked.push(item);
        } else {
          newChecked.splice(currentIndex, 1);
        }
        setChecked(newChecked);
      }
    });
    //console.log(...checked, selectedObject);
  }

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
    console.log(newChecked);
    setSelectedShape(newChecked);

    // for (var i = 0; i < newChecked.length; i++)
    //   setSelectedShape(newChecked[i].id);
  };

  return (
    <List
      sx={{
        width: "100%",
        maxWidth: 280,
        bgcolor: "background.paper",
        position: "relative",
        overflow: "auto",
        maxHeight: 300,
        "& ul": { padding: 0 },
      }}
    >
      {layerItmes.map((value) => {
        const labelId = `checkbox-list-label-${value.id}`;

        return (
          <ListItem
            key={value.id}
            secondaryAction={
              <IconButton edge="end" aria-label="comments">
                <CommentIcon />
              </IconButton>
            }
            disablePadding
          >
            <ListItemButton
              role={undefined}
              onClick={handleToggle(value)}
              dense
            >
              <ListItemIcon>
                <Checkbox
                  edge="start"
                  checked={checked.indexOf(value) !== -1}
                  tabIndex={-1}
                  disableRipple
                  inputProps={{ "aria-labelledby": labelId }}
                />
              </ListItemIcon>
              <ListItemText
                id={labelId}
                primary={value.name}
                style={{ fontSize: "20px" }}
              />
            </ListItemButton>
          </ListItem>
        );
      })}
    </List>
  );
}
